import React, { Component } from "react";
import { Link } from "react-router-dom";
import MyFooter from "../../MyFooter/MyFooter";
import Paginator from "react-js-paginator";
import "./style.css";
import {
  actFetchTransactionsRequest,
  actFindTransactionsRequest,
} from "../../../redux/actions/transaction";
import { connect } from "react-redux";
import { exportExcel } from "../../../utils/exportExcel";
import { formatNumber } from "../../../utils";

let token;
class Transaction extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchText: "",
      total: 0,
      currentPage: 1,
    };
  }
  componentDidMount() {
    this.fetch_reload_data();
  }

  fetch_reload_data() {
    token = localStorage.getItem("_auth");
    this.props
      .fetch_transaction(token)
      .then((res) => {
        this.setState({
          total: res.total,
          transactions: res.results,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }

  pageChange(content) {
    const limit = 10;
    const offset = limit * (content - 1);
    this.props.fetch_transaction(token, offset);
    this.setState({
      currentPage: content,
    });
    window.scrollTo(0, 0);
  }

  handleSubmit = (event) => {
    event.preventDefault();
    const { searchText } = this.state;
    this.props.find_transactions(token, searchText).then((res) => {
      this.setState({
        total: res.total,
        transactions: res?.results || []
      });
    });
  };

  handleChange = event => {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    this.setState({
      [name]: value
    });
  };

  downloadExcel = () => {
    const key = "transactions";
    exportExcel(key);
  };

 convertToVietnamTime = (dateString) => {
    const year = dateString.substring(0, 4);
    const month = dateString.substring(4, 6);
    const day = dateString.substring(6, 8);
    const hour = dateString.substring(8, 10);
    const minute = dateString.substring(10, 12);
  
    const date = new Date(`${year}-${month}-${day}T${hour}:${minute}:00`);
 
    const options = {
      timeZone: "Asia/Ho_Chi_Minh", 
      year: "numeric",
      month: "long",
      day: "numeric",
      hour: "numeric",
      minute: "numeric",
      hour12: false,
    };
  
    return date.toLocaleString("vi-VN", options);
  };

  render() {
    let { transactions } = this.state;
    const { searchText, total } = this.state;
    const vnPayStatus = {
      "00": "Giao dch thnh cng",
      "07": "Tr tin thnh cng. Giao dch b nghi ng (lin quan ti la o, giao dch bt thng).",
      "09": "Giao dch khng thnh cng do: Th/Ti khon ca khch hng cha ng k dch v InternetBanking ti ngn hng.",
      10: "Giao dch khng thnh cng do: Khch hng xc thc thng tin th/ti khon khng ng qu 3 ln",
      11: "Giao dch khng thnh cng do:  ht hn ch thanh ton. Xin qu khch vui lng thc hin li giao dch.",
      12: "Giao dch khng thnh cng do: Th/Ti khon ca khch hng b kha.",
      13: "Giao dch khng thnh cng do Qu khch nhp sai mt khu xc thc giao dch (OTP). Xin qu khch vui lng thc hin li giao dch.",
      24: "Giao dch khng thnh cng do: Khch hng hy giao dch",
      51: "Giao dch khng thnh cng do: Ti khon ca qu khch khng  s d  thc hin giao dch.",
      65: "Giao dch khng thnh cng do: Ti khon ca Qu khch  vt qu hn mc giao dch trong ngy.",
      75: "Ngn hng thanh ton ang bo tr.",
      79: "Giao dch khng thnh cng do: KH nhp sai mt khu thanh ton qu s ln quy nh. Xin qu khch vui lng thc hin li giao dch",
      99: "Cc li khc (li cn li, khng c trong danh sch m li  lit k)",
    };
    return (
      <div className="content-inner">
        {/* Page Header*/}
        <header className="page-header">
          <div className="container-fluid">
            <h2 className="no-margin-bottom">Danh sch giao dch</h2>
          </div>
        </header>
        {/* Breadcrumb*/}
        <div className="breadcrumb-holder container-fluid">
          <ul className="breadcrumb">
            <li className="breadcrumb-item">
              <Link to="/">Home</Link>
            </li>
            <li className="breadcrumb-item active">Danh sch giao dch</li>
          </ul>
        </div>
        <section className="tables pt-3">
          <div className="container-fluid">
            <div className="row">
              <div className="col-lg-12">
                <div className="card">
                  <div className="card-header d-flex align-items-center">
                  <button
                      onClick={this.downloadExcel}
                      style={{
                        border: 0,
                        background: "green",
                        borderRadius: 3,
                      }}>
                      <h3
                        className="fa fa-file-excel-o"
                        style={{
                          fontSize: 18,
                          color: "white",
                          padding: "5px 6px",
                        }}>
                        {" "}
                        Xut Excel
                      </h3>
                    </button>
                  </div>
                  <form
                    onSubmit={(event) => this.handleSubmit(event)}
                    className="form-inline md-form form-sm mt-0"
                    style={{
                      justifyContent: "flex-end",
                      paddingTop: 5,
                      paddingRight: 20,
                    }}>
                    <div>
                    <input
                        name="searchText"
                        onChange={this.handleChange}
                        value={searchText}
                        className="form-control form-control-sm ml-3 w-75"
                        type="text"
                        placeholder="Search"
                        aria-label="Search"
                      />

                      <button
                        style={{
                          border: 1,
                          background: "green",
                          color: "white",
                        }}>
                        {" "}
                        <i className="fa fa-search" aria-hidden="true"></i>
                      </button>
                    </div>
                  </form>
                  <div className="card-body">
                    <div className="table-responsive">
                      <table className="table table-hover">
                        <thead>
                          <tr>
                            <th>STT</th>
                            <th>M giao dch</th>
                            <th>M n hng</th>
                            <th>Tng tin</th>
                            <th>Thi gian</th>
                            <th>Trng thi</th>
                            <th>Phng thc thanh ton</th>
                            {/* <th style={{ textAlign: "center" }}></th> */}
                          </tr>
                        </thead>
                        <tbody>
                          {transactions && transactions.length
                            ? transactions.map((item, index) => {
                                const statusColor =
                                  item?.status === "00" ? "green" : "red";
                              
                                return (
                                  <tr key={index}>
                                    <th scope="row">{index + 1}</th>
                                    <td>{item?.transactionCode}</td>
                                    <td>{item?.orderId}</td>
                                    <td>{formatNumber.format(item?.amount)}</td>
                                    <td>
                                      {this.convertToVietnamTime(
                                        item.paymentDate
                                      )}
                                    </td>
                                    <td style={{ color: statusColor }}>
                                      {vnPayStatus[item?.status]}
                                    </td>
                                    <td style={{color: '#00CCFF', fontWeight: 'bolder'}}>{item.type}</td>
                                  </tr>
                                );
                              })
                            : null}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
                <nav
                  aria-label="Page navigation example"
                  style={{ float: "right" }}>
                  <ul className="pagination">
                    <Paginator
                      pageSize={10}
                      totalElements={total}
                      onPageChangeCallback={(e) => {
                        this.pageChange(e);
                      }}
                    />
                  </ul>
                </nav>
              </div>
            </div>
          </div>
        </section>
        {/* Page Footer*/}
        <MyFooter></MyFooter>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    transactions: state.transactions,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    fetch_transaction: (token, offset) => {
      return dispatch(actFetchTransactionsRequest(token, offset));
    },
    find_transactions: (token, searchText) => {
      return dispatch(actFindTransactionsRequest(token, searchText));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Transaction);
